@import "../../../global.scss" ;

.contact{


  .wrapper{
    padding:5px;
    >.heading{
      h1{
        text-align: center;
      }
    }
    >.icons{
      text-align: center;


      >*{
        margin:0 1rem;
        font-size: x-large;
      }
    }

    .contact-mail{
      text-align: center;
    }
  }
}


@media screen and (min-width:$breakpoint) {
  .contact{
    display:flex;
    align-items: center;
    justify-content: center;
    >.wrapper{
      width:50%;
    }
  }
}