@import "../../../global.scss";

.slideshow {
  width: 100%;
  padding: 10px;

  .wrapper {

    .right {
      width: 100%;

      > .slid-con {
        position: relative;
        width: 100%;
        > .img-con {
          display: none;
          width: 100%;
          height: 70%;
          -webkit-animation-name: fade;
          -webkit-animation-duration: 1.5s;
          animation-name: fade;
          animation-duration: 1.5s;

          img {
            max-width: 100%;
            max-height: 100%;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
          }

          &.active {
            display: block;
          }
        }
        @-webkit-keyframes fade {
          from {
            opacity: 0.4;
          }
          to {
            opacity: 1;
          }
        }

        @keyframes fade {
          from {
            opacity: 0.4;
          }
          to {
            opacity: 1;
          }
        }

        > button {
          all: unset;
          position: absolute;
          top: 50%;
          font-size: xx-large;
          background-color: $nav-color;
          color: #fff;
          opacity: 0.5;
          padding: 20px;
          box-shadow: 1px 1px 10px 2px #000;
          transition: ease 0.5s;
          cursor: pointer;
          border-radius: 5px;
          &:hover {
            opacity: 0.8;
          }

          &.next {
            right: 0;
          }
        }

        .dotcon {
          display: flex;
          align-items: center;
          justify-content: center;

          > .dots {
            width: 50%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-around;
            text-align: center;
            padding: 20px;

            > .dot {
              padding: 10px;
              background-color: $nav-color;
              opacity: 0.7;
              cursor: pointer;
              transition: ease 0.5s;
              border-radius: 50%;

              &:hover {
                opacity: 0.8;
              }

              &.active {
                opacity: 1;
              }
            }
          }
        }
      }
    }
  }
}



@media screen and (max-width:$breakpoint) {
  .slideshow{
    >.wrapper{
      display: block;
      width:100%;
      .left{
        width: 100%;
        height: 25%;
      }
      .right{
        width:100%;
        height: 60%;
      }
    }
  }
}