@import "../../../global.scss";

.navbar {
  width: 100%;

  > .wrapper {
    width: 100%;
    padding: 0 2rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    z-index: 1000;
    transition: $transtime1;
    color: $main-color;
    line-height: $nav-height;

    > .left {
      display: flex;
      align-items: center;
      justify-content: center;

      .mail a {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 3rem;
        text-decoration: none;
        color: $main-color;
      }

      .name {
        font-size: xx-large;
      }
    }
    > .right {

      > .hamburger {
        padding: 0 25px;
        cursor: pointer;

        > * {
          font-size: xx-large;
          transition: ease 1s;
        }
      }
    }
    > .menu {
      position: fixed;
      top: 0;
      left: -300vh;
      background-color: $main-color;
      height: 100%;
      padding: 10px;
      transition: $transtime1;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: -1;
      box-shadow: 1px 1px 10px 1px $main-color;

      width: 50%;

      ul {
        list-style-type: none;

        display: flex;
        flex-direction: column;
        width: 100%;
        text-align: center;

        li {
          display: flex;
          width: 100%;

          a {
            display: block;
            width: 100%;
            padding: 20px;
            text-decoration: none;
            color: #ccc;
            font-size: larger;

            &:hover {
              color: #fff;
              font-size: x-large;
            }
          }
        }
      }
    }

    &.active {
      background-color: $main-color;
      color: #fff;

      > .menu {
        left: 0;
      }
    }
  }
}

@media screen and (max-width: $breakpoint) {
  .navbar {
    > .wrapper {
      padding: 0 15px;
      background-color: #fff;

      .mail {
        display: none;
      }
      .menu {
        width: 80%;
      }

      > .right {
        > .hamburger {
          padding: 0 8px;
        
        }
      }
    }
  }
}
