@import "../../../global.scss";

.intro {
  > .wrapper {
    .intro2 {
      display: flex;
      height: 100%;

      .left {
        flex: 0.5;
        overflow: hidden;
        padding: 10px;
        display: flex;
        background-color: yellow;
        border-radius: 10% 50% 21%;

        > .imgcon {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;

          img {
            max-width: 100%;
            max-height: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 100%;
          }
        }
      }
      > .right {
        flex: 0.5;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 50px ;

        > .wrapper {
          width: 100%;
          display: flex;
          align-items: center;
          padding-top: 2rem;
          flex: 0.7;

          > p {
            .greet {
              font-size: xx-large;
            }
            .name {
              font-size: xxx-large;
              font-weight: 900;
              color: crimson;
            }
            .changing-text {
              font-size: x-large;

              .ityped-cursor {
                animation: blink 1s infinite;
              }

              @keyframes blink {
                50% {
                  opacity: 1;
                }
                100% {
                  opacity: 0;
                }
              }
            }
          }
        }

        > .expand {
          width: 100%;
          flex: 0.3;
          > a {
            > .expand-icon {
              font-size: 10rem;
              color: #222;
              animation: arrowBlink 2s infinite;

              &:hover {
                color: black;
              }
            }
            @keyframes arrowBlink {
              0% {
                opacity: 0%;
              }
              50% {
                opacity: 80%;
              }
              100% {
                opacity: 0%;
              }
            }
          }
        }
      }
    }
  }
}


@media screen and (max-width:$breakpoint) {
  .intro{
    >.wrapper{

      .intro2{
        display:block;
      
      }
      .left{
        width:100%;
        height: 50%;
      }
      .right{
        height: 50%;
        width:100%;
      }
    }
  }
}
