@import "../../global.scss" ;


.a1exporter{
  height: 100vh;

  

    >.wrapper{
      width: 100%;
      position: relative;
      height: calc(100vh - 3.5rem) ;
      top: 3.5rem;
      scroll-snap-type: y mandatory;
      overflow: scroll;
      scroll-behavior: smooth;
      scrollbar-width: none;

      &::-webkit-scrollbar{
        display:none ;
      }

      
      >*{
        
        padding: 20px;
        width: 100vw;
        height: calc(100vh - 3.5rem);
        scroll-snap-align: start;
      }
    }

    
  
}






@media screen and (max-width:$breakpoint) {
  .a1exporter{

    >.wrapper{
      scroll-snap-type: none;
    }
  }
}