@import "../../../global.scss";

.form76 {
  width: 100%;

  > .wrapper {
    width: 100%;

    > .inputs {
      width: 100%;

      input {
        width: 100%;
        padding: 10px;
        margin: 1rem 0;
        border:none;
        border-bottom: 2px solid $nav-color;
        background-color: transparent;
      }
      textarea {
        width: 100%;
        margin: 1rem 0;
        padding: 10px;
        border: solid 1px $nav-color;
        
      }
    }

    > .btn {
      width:100%;
      display: flex;
      justify-content: center;
   
      button {
        all:unset;
        padding: 10px 20px;
        border-radius: 5px;
        background-color: $nav-color;
        color: #fff;
        cursor: pointer;

        
      }
    }

  }
}
